import React from 'react'
import Conectar from './Conectar'
import Minting from './Minting'
import store from '../store/reducers'

export const Main = ({allData}) => {



return (
    <div className=' w-full flex justify-center '>
    {store.getState().blockchain.account === "" || store.getState().blockchain.contracNFT === null ?
        (
            <Conectar allData = {allData}></Conectar>
        ):(
            <Minting allData= {allData}></Minting>
        )}
    </div>
)
}
