import logo from './logo.svg';
import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import {BrowserRouter, Route, Routes} from 'react-router-dom'
import Adm from './components/adm';
import { Main } from './components/Main';
import Menu  from './components/Menu';
import Footer from './components/Footer';


function App() {

  const dispatch = useDispatch()
  const blockchain = useSelector((store)=>store.blockchain)
  const dataUser = useSelector((store)=>store.dataUser)

  let allData = {
      blockchain : blockchain,
      dataUser : dataUser
  }

  return (
    <>
      <div name="Layout" className='w-full h-screen grid grid-cols-1'>
        <Menu className= "w-full h-90"/>
        <Main allData = {allData} className= "w-full h-screen"/>
        <Footer className= "w-full h-3"/>
      </div>
        
      <BrowserRouter>
        <Routes>
          <Route path="/cstoryadm" element={<Adm/>}/>
        </Routes>
      </BrowserRouter>

        
    </>
  );
}

export default App;
