import { createSlice } from "@reduxjs/toolkit";
import { ethers} from "ethers";
import ContratoNFT from "../../contracts/abi.json"
import TokenPruebasAbi from "../../contracts/tokenPruebas.json"
import { fetchData } from "./sliceData";


// ESTADO INICIAL Y CAMBIO DE ESTADO EN REDUCERS 

const blockchainSlice = createSlice({
    name:'blockchain',
    initialState:{
        login: false,
        account: null,
        contracNFT : null ,
        TokenPruebas : null,
            errorMsg: ""
    },
    reducers: {

        conectado: (state, action)=>{
            state.login = action.payload.login
            state.account = action.payload.account
            state.contracNFT = action.payload.contratoNFT
            state.TokenPruebas = action.payload._TokenPruebas
        }
    }
})


// ACTIONS 

const {conectado} = blockchainSlice.actions


export const conexion= ()=>{

    return async (dispatch)=>{

        try {
           //CONECTAR A METAMASK Y ADQUIRIR NUMERO DE RED

            const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
            await provider.send("eth_requestAccounts", []);
            const signer = provider.getSigner();
            let userAddress = await signer.getAddress();

            const networkId = await window.ethereum.request({
            method: "net_version"
            })
    
            // GET CONTRATO 

            const contractAddress = "0xe2bcB814B7552668bbc2Ad3dc4903A455F6D26a0"       
            const contractTokenPruebas = "0x8691b2C7587e874747Bc2739CC35D37de968Fa2D"
            
            const NftContract = await new ethers.Contract(contractAddress, ContratoNFT, signer)
            const TokenPruebas = await new ethers.Contract(contractTokenPruebas, TokenPruebasAbi, signer)


            //EXTRACION DATOS DEL CONTRATO 

            if(networkId == 4){
                
            dispatch(conectado({
                login: true,
                account: userAddress,   
                contratoNFT: NftContract,
                _TokenPruebas: TokenPruebas  
            })) 
            
            } else{
                console.log('CONECTED TO THE WORONG NETWORK')
            }

            fetchData(dispatch, userAddress, NftContract)
            
        } catch (err) {
            console.log(err)
            console.log('it possible that you havent metamask in your browser')
            
        }
    }

}


export default blockchainSlice.reducer