import React, {useEffect} from 'react'
import { ethers } from 'ethers'
import { useDispatch } from 'react-redux';
import { fetchData } from '../store/slices/sliceData';
import { NFT } from './NFT';
import { conexion } from '../store/slices/sliceBlockchain';




const Minting = ({allData}) => {



  const {blockchain, dataUser} = allData 

  const dispatch = useDispatch()

    // ALLOWANCE 

  const _spender = "0xe2bcB814B7552668bbc2Ad3dc4903A455F6D26a0"
  const _amount = "10000000000000000000000"

  const allowance = async ( _rarity, _account)=>{
    
    const taxB = await blockchain.TokenPruebas.allowance(_account, _spender)
    .then((resp)=>{
  
      const respuesta = Number(resp._hex).toLocaleString('fullwide', { useGrouping: false })
      console.log(resp)
      if(respuesta < 40000000000000000000){
        const minteo = ()=>{
          const txC =  approve( _rarity, _account)
        }
        minteo()
      }else{
        minter( _rarity, _account)  
      } 
    })
  }

    // APPROVE
    
  const approve = async ( _rarity, _account)=>{

      try {
        const txA = await blockchain.TokenPruebas.approve( _spender, _amount)
        await txA.wait().then((resp)=>{    
          if(txA){
            minter( _rarity, _account)
          }
        }
      ) 
      } catch (error) {
        console.log(error)
        console.log('SE JODIO')
      }
  }

    //Funcion de minteo de NFT 

  const minter = async( _rarity, _account)=>{

    //SET PRECIO DE LA CANTIDAD DE DINERO QUE SE ENVIARA
    const option = {
      from: _account,
      value: "20000000000000000000"
    }
    // MINTEO A LA BLOCKCHAIN
    try {
      const tx = await blockchain.contracNFT.mint( _rarity, option.value)
      await tx.wait().then((resp)=>{
        if(resp){
          console.log('Your NFT has been minted')
        }
        else{
          console.log('Something has been happened')
        }
        }) 
    } catch (error) {
      console.log(error)
    } 

  }

  // HANDLER CONNECT

  const handlerConnect = ( _rarity, _account)=>{
    allowance( _rarity, _account)
  }

  // FETCH NFT 
  useEffect(()=>{
    fetchData(dispatch ,blockchain.account, blockchain.contracNFT)
    const interval = setInterval(()=>{
      fetchData(dispatch ,blockchain.account, blockchain.contracNFT)
    },5000)

    return()=>clearInterval(interval)
  },[]) 

  return (
      <div className=''>
        <div className=' mt-40 h-1/6 w-full flex justify-center content-center'>
          <p>
            <button 
              onClick={(e)=>{
                e.preventDefault()
                  handlerConnect( 1, blockchain.account)
              }}
              className =" bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" >
              Mint
              Commont
            </button>
          </p>
          <p>
            <button 
              onClick={(e)=>{
                e.preventDefault()
                  handlerConnect( 2, blockchain.account)
              }}
              className =" bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" >
              Mint
              Rare
            </button>
          </p>
          <p>
            <button 
              onClick={(e)=>{
                e.preventDefault()
                  handlerConnect( 3, blockchain.account)
              }}
              className =" bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" >
              Mint
              Epic
            </button>
          </p>
          <p>
            <button 
              onClick={(e)=>{
                e.preventDefault()
                  handlerConnect( 4, blockchain.account)
              }}
              className =" bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" >
              Mint
              Legendary
            </button>
          </p>
          <p>
            <button 
              onClick={(e)=>{
                e.preventDefault()
                  handlerConnect( 5, blockchain.account)
              }}
              className =" bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" >
              Mint
              Supreme
            </button>
          </p>
        </div>

        <div>
          <NFT dataNft = {dataUser} ></NFT>
        </div>

      </div>           
)}

export default Minting