import React from 'react'
import { useDispatch } from 'react-redux'
import { conexion } from '../store/slices/sliceBlockchain'

const Conectar = () => {

  const dispatch = useDispatch()

  return (
    <div className='w-full h-full flex justify-center'>
      <button
        
        onClick={ (e)=>{
          e.preventDefault()
          dispatch(conexion())
        }}
        
      className = "bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded w-300 h-10"
      >Conectar</button>
    </div>
  )
}

export default Conectar