import { createSlice } from '@reduxjs/toolkit';
import { ContractFactory, ethers } from 'ethers'
import ContratoNFT from "../../contracts/abi.json"

const dataUserSlice = createSlice({
    name:'dataUser',
    initialState:{
        OwnerNFTs: [],
        NftData: [],
        errorMsg: "",
    },
    reducers: {

        getData: (state, action)=>{
            state.OwnerNFTs = action.payload.allOwnerNfts
            state.NftData = action.payload._NftData
        }
    }
})

const {getData} = dataUserSlice.actions


export const fetchData = async (dispatch, _account, _contrato)=>{
    
            // Funcion de traer los NFT desde la blockchains con Ethers 

            if(_contrato){
                
                let arrayMetada= new Array()
                arrayMetada = []
                let nftBalance = await _contrato.balanceOf(_account)
                let nftBalanceConverted = parseInt(nftBalance)
                
                if(nftBalanceConverted == 0 && _contrato == null){
                    
                }else{
                    let getOwnerTokens = await _contrato.get_ownerTokens(_account) 
                    
                    for(let i =0; i < nftBalanceConverted; i++){
                
                        let tokenMetadataURI =  await _contrato.tokenURI(parseInt(getOwnerTokens[i]._hex))
                        if(tokenMetadataURI.startsWith('ipfs://')){ 
                        tokenMetadataURI = `https://ipfs.io/ipfs/${tokenMetadataURI.split("ipfs://")[1]}`
                        }         
                        
                        const tokenMetada = await fetch(tokenMetadataURI).then((resp)=>resp.json())
                        arrayMetada.push(tokenMetada) 
                    }

                    dispatch(getData({
                        allOwnerNfts: getOwnerTokens,
                        _NftData: arrayMetada
                    }))  
                }
            }
        }

export default dataUserSlice.reducer